.PlayerRobotSVG {
    display: flex;
    .playerFace,
    .robotFace {
        height: 2.5rem;
        width: 2.5rem;
        border:0.125rem solid #000;
        border-radius: 50%;
        position: relative;
    }
    .playerFace, 
    .robotFace {
        &::after,
        &::before {
            content: '';
            position: absolute;
            top: 0.625rem;
            height: 0.375rem;
            width: 0.125rem;
            background-color: #000;
        }
        &::after {
            left:0.75rem;
        }
        &::before {
            right:0.75rem;
        }
    }
    .playerFace {
        transform: translateX(0.5rem);
        background-color: $red;
        z-index: 9;
        span {
            position: absolute;
            height:1.688rem;
            width:1.688rem;
            border:0.125rem solid;
            border-radius: 50%;
            border-color: transparent transparent #000 #000;
            transform: rotate(315deg) translate(0.063rem, 0.375rem);
        }
    }
    .robotFace {
        span {
            position: absolute;
            height:0.125rem;
            width: 1.375rem;
            transform: translate(0.438rem, 1.125rem);
            background-color: #000;
        }
    }
}

.TwoFaces {
    display: flex;
    .happyFace:first-child {
        transform: translateX(0.5rem);
        background-color: $yellow;
        z-index: 9;
    }
}
.happyFace {
    height:2.5rem;
    width:2.5rem;
    border:0.125rem solid #000;
    border-radius: 50%;
    position: relative;
    &::after,
    &::before {
        content: '';
        position: absolute;
        top: 0.625rem;
        height: 0.375rem;
        width:0.125rem;
        background-color: #000;
    }
    &::after {
        left: 1.063rem;
    }
    &::before {
        right: 0.5rem;
    }
    span {
        position: absolute;
        height:1.688rem;
        width:1.688rem;
        border:0.125rem solid;
        border-radius: 50%;
        border-color: transparent transparent transparent #000;
        transform: rotate(270deg) translate(0.063rem, 0.563rem);
    }
}