// FramWorks
.center-f {
    display: flex;
    align-items: center;
}
.center-g {
    display: grid;
    place-items: center;
}
.position-flow {
    position: absolute;
    top: 0;
    left: 0;
}
.smoothPointer {
    transition: .5s;
    cursor: pointer;
}

.container {
    margin: 0 auto;
}

