.intro {
    height: 100%;
    @extend .center-g;
    user-select: none;
    .intro-wrapper {
        text-align: center;
        .hideUp {
            transform: translateY(-1.875rem);
            opacity: 0;
            transition: .5s;
        }
        .show {
            transform: translateY(0);
            opacity: 1;
            transition: .5s;
        }
        img {
            height: 4.375rem;
            margin-bottom: 4rem;
        }
        .buttons {
            display: flex;
            flex-direction: column;
            button {
                height: 4.063rem;
                width: 25rem;
                border: 0.125rem solid #000;
                border-radius: 0.938rem;
                box-shadow: 0 0.438rem 0 #000;
                margin: 0.45rem 0;
                font-size: 1.25rem;
                font-weight: 800;
                text-transform: uppercase;
                text-align: start;
                padding: 0 1.25rem;
                cursor: pointer;
                display: flex;
                justify-content: space-between;
                align-items: center;
                &:hover {
                    filter: brightness(1.1);
                }
                &:active {
                    transform: translateY(0.375rem);
                    box-shadow: unset;
                }
            }
            button:nth-child(1) {
                background-color: $red;
                color: #fff;
            }
            button:nth-child(2) {
                background-color: $yellow;
            }
            button:nth-child(3) {
                background-color: #fff;
            }
        }
        .gameRules {
            position: fixed;
            top: 50%;
            left: 50%;
            width: 37.5rem;
            transform: translate(-50%, -50%);
            display: none;
            @media (max-width: 48rem) {
                & {
                    width: 95%;
                }
            }
            .gameRules_Wrapper {
                background-color: #fff;
                padding: 2rem 1.65rem 3rem;
                border-radius: 1.563rem;
                border: 0.188rem solid #000;
                box-shadow: 0 0.438rem 0 #000;
                transition: .5s;
                transform: scale(0);
                h1 {
                    font-size: 2.625rem;
                    text-transform: uppercase;
                }
                .objective,
                .howToPlay  {
                    text-align: start;
                    h4 {
                        color: #5529c5;
                        font-size: 1.125rem;
                        text-transform: uppercase;
                        margin-bottom: 0.65rem;
                    }
                    p {
                        font-weight: 500;
                    }
                }
                .objective {
                    margin: 1.25rem 0 1rem;
                    p {
                        line-height: 1.563rem;
                    }
                }
                .howToPlay {
                    .line {
                        display: flex;
                        margin: 1rem 0;
                        span {
                            font-weight: 800;
                            margin-right: 1rem;
                        }
                    }
                }
                .button_wrapper {
                    position: absolute;
                    bottom: -1.875rem;
                    left: 50%;
                    transform: translateX(-50%);
                    button {
                        @extend .center-g;
                        @extend .smoothPointer;
                        height: 3.75rem;
                        width: 3.75rem;
                        background-color: #fa6787;
                        border: 0.188rem solid #000;
                        border-radius: 50%;
                        box-shadow: 0 0.313rem 0 #000;
                        &:active {
                            transform: scale(.93);
                        }
                        &::after,
                        &::before {
                            content: '';
                            height: 0.188rem;
                            width: 2.188rem;
                            background-color: #000;
                            position: absolute;
                        }
                        &::after {
                            transform: rotate(45deg);
                        }
                        &::before {
                            transform: rotate(-45deg);
                        }
                    }
                }
                @media (max-width: 37.5rem) {
                    & {
                        padding: 1.625rem 1.375rem 2.25rem;
                    }
                    h1 {
                        font-size: 2rem;
                    }
                    p {
                        font-size: 0.875rem;
                    }
                    .objective,
                    .howToPlay {
                        h4 {
                            font-size: 1rem;
                        }
                        .line {
                            span {
                                    margin-right: 0.625rem;
                            }
                        }
                    }
                }
            }
        }
        @media (max-width: 37.5rem) {
            img {
                height: 3.125rem;
            }
            .buttons {
                button {
                    height: 3.438rem;
                    width: 18.75rem;
                    font-size: 1rem;
                    box-shadow: 0 0.313rem 0 #000;
                    padding: 0 1rem;
                    .PlayerRobotSVG, 
                    .TwoFaces {
                        transform: scale(.9);
                    }
                }
            }
        }
    }
}

